/* Reset and base styles  */
* {
	padding: 0px;
	margin: 0px;
	border: none;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

/* Links */

a, a:link, a:visited  {
  text-decoration: none;
}

a:hover  {
  text-decoration: none;
}

/* Common */

aside, nav, footer, header, section, main {
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
	font-weight: inherit;
}

ul, ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

img, svg {
	max-width: 100%;
	height: auto;
}

address {
  font-style: normal;
}

/* Form */

input, textarea, button, select {
	font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
	display: none;
}

button, input[type="submit"] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input[type="text"],
input[type="email"],
input[type="password"],
textarea{
  border: 1px solid grey;
}

input:focus, input:active,
button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}

img{
 vertical-align: top;
}
body {
  font: 16px/1.2 Arial, "Times New Roman";
}

.wrapper {
  max-width: 1024px;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
  "header header"
  "nav content";
  grid-template-rows: 100px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  padding-bottom: 24px;
}

.content {
  grid-area: content;
}

button,
input[type="submit"],
.button{
  background: darkred;
  color: white;
  padding: 10px 16px;
  border-radius: 4px;
}

button:disabled,
input[type="submit"]:disabled,
.button:disabled{
  background: rgba(68, 0, 0, 0.73);
  cursor: none;
}

a {
  color: darkred;
}

a:visited {
  color: black;
}
